<template>
  <div class="shop-register">
    <h2>店铺注册</h2>
    <el-form
      ref="formRef"
      :model="form"
      :rules="rules"
      label-width="120px"
      @submit.prevent="handleSubmit"
    >
      <el-form-item label="店铺名称" prop="name">
        <el-input v-model="form.name" placeholder="请输入店铺名称" />
      </el-form-item>
      <el-form-item label="宣传标语" prop="slogan">
        <el-input
          v-model="form.slogan"
          type="textarea"
          placeholder="例如: VIP尊享满89元减4元运费券"
        />
      </el-form-item>
      <el-form-item label="运费(元)" prop="expressPrice">
        <el-input-number v-model="form.expressPrice" :min="0" :precision="2" />
      </el-form-item>
      <el-form-item label="免运费门槛(元)" prop="expressLimit">
        <el-input-number v-model="form.expressLimit" :min="0" :precision="2" />
      </el-form-item>
      <el-form-item label="销售额(元)" prop="sales">
        <el-input-number v-model="form.sales" :min="0" :precision="2" />
      </el-form-item>
      <el-form-item label="店铺图片" prop="img">
        <el-upload
          :on-change="handleLogoChange"
          :before-upload="beforeLogoUpload"
          :show-file-list="false"
          :auto-upload="false"
        >
          <el-button type="primary">点击上传</el-button>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">提交注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { useRouter } from "vue-router";
import { post } from "@/utils/request";
import { ElMessage } from "element-plus";

const router = useRouter();

const form = ref({
  name: "",
  slogan: "",
  expressPrice: 0,
  expressLimit: 0,
  sales: 0,
  img: ""
});

const rules = {
  name: [{ required: true, message: "请输入店铺名称", trigger: "blur" }],
  slogan: [{ required: true, message: "请输入宣传标语", trigger: "blur" }],
  expressPrice: [{ required: true, message: "请输入运费", trigger: "blur" }],
  expressLimit: [
    { required: true, message: "请输入免运费门槛", trigger: "blur" }
  ],
  sales: [{ required: true, message: "请输入销售额", trigger: "blur" }]
};

const handleLogoChange = file => {
  form.value.img = file;
};

const beforeLogoUpload = file => {
  const isImage = file.type.startsWith("image/");
  if (!isImage) {
    ElMessage.error("只能上传图片文件!");
  }
  return isImage;
};

const handleSubmit = async () => {
  try {
    await post("/api/shop", form.value, !!form.value.img);
    ElMessage.success("店铺注册成功");
    router.push("/");
  } catch (error) {
    ElMessage.error(error.message || "店铺注册失败");
  }
};
</script>

<style scoped>
.shop-register {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}
</style>
